import 'bootstrap/dist/css/bootstrap.min.css';
import Table from 'react-bootstrap/Table';
import './ItemTable.css'
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import SwapVertIcon from '@mui/icons-material/SwapVert';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';
import { useState } from 'react';
import { Menu, MenuItem, Checkbox, IconButton, Button } from '@mui/material';
import FilterAltIcon from '@mui/icons-material/FilterAlt';
import Slider from 'rc-slider'
import 'rc-slider/assets/index.css';

function ItemTable({ datas, sortConfig, setSortConfig, onSortChange, onFilterApply, setFilterValues, filterValues, setGapRangeValues, gapRange, setGapRange }) {

    const fields = ['지역', '아파트명', '갭', '매매가', '전세가', '세대수', '년도', '평',
        '층', '입주가능날짜', '추가정보','부동산 번호'];
    
    // sortConfig 아래에 새로운 state 추가
    const [showGapSlider, setShowGapSlider] = useState(false);
    const [anchorEl, setAnchorEl] = useState(null); // 필터 메뉴가 열리는 위치

    const fieldMappings = {
        '지역': 'region',
        '아파트명': 'name',
        '갭': 'gapPrice',
        '매매가': 'price',
        '전세가': 'previousDeposit',
        '평': 'apartmentSize',
        '층': 'floor',
        '입주가능날짜': 'availableDate',
        '부동산 번호': 'agencyPhoneNumber',
        '추가정보': 'description',
        '상세 위치': 'location',
        '년도' : 'year',
        '세대수' : 'houseHoldCount'
    };
    const handleSort = (field) => {
        let direction = 'DESC';
        if (sortConfig.key === fieldMappings[field] && sortConfig.direction === 'DESC') {
            direction = 'ASC';
        }
        setSortConfig({ key: fieldMappings[field], direction });
        onSortChange(fieldMappings[field], direction);
    };

    const getSortIcon = (field) => {

        if (sortConfig.key === fieldMappings[field]) {
            return sortConfig.direction === 'ASC' ? <ArrowUpwardIcon fontSize="small" /> : <ArrowDownwardIcon fontSize="small" />;
        }
        return <SwapVertIcon fontSize="small" />;
    }

    const handleNameClick = (itemNumber) => {

        window.open('https://fin.land.naver.com/articles/' + itemNumber, '_blank');
    };

    // 필터 메뉴 토글
    const handleFilterClick = (event, field) => {
        
        if (field === '갭') {
            setShowGapSlider(!showGapSlider);
        }else{
            setAnchorEl(event.currentTarget);
        }
    };

    const handleFilterClose = () => {
        setAnchorEl(null); // 메뉴 닫기
    };

    // 새로운 함수 추가
    const handleGapRangeChange = (value) => {
        setGapRange(value);
    };

    // 필터 적용 핸들러
    const handleGapFilterApply = (e) => {
        e.stopPropagation();
        setShowGapSlider(false);
        setGapRangeValues(gapRange);
    };

    // 체크박스 상태 관리
    const handleFilterChange = (value) => (event) => {
        const newFilterValues = [...filterValues];
        if (event.target.checked) {
            newFilterValues.push(value);
        } else {
            const currentIndex = newFilterValues.indexOf(value);
            if (currentIndex > -1) {
                newFilterValues.splice(currentIndex, 1);
            }
        }
        setFilterValues(newFilterValues);
    };

    // 필터 적용 버튼 클릭 시 호출되는 함수
    const handleFilterApply = () => {
        onFilterApply(filterValues); // 선택된 필터 값을 부모로 전달
        console.log(filterValues)
        handleFilterClose(); // 메뉴 닫기
    };

    const formatCurrency = (value) => {
        if (value == 0 | !value)
            return '0원'

        // 음수인 경우 부호 처리
        const isNegative = value < 0;
        const absValue = Math.abs(value);
        
        const billion = Math.floor(absValue / 100000000); // 억 단위
        const million = Math.floor((absValue % 100000000) / 10000); // 만 단위
        const remainder = absValue % 10000; // 원 단위

        let result = '';
        
        // 음수 부호 추가
        if (isNegative) {
            result += '-';
        }

        if (billion > 0) {
            result += `${billion}억`;
        }
        if (million > 0) {
            result += ' '
            result += `${million}만`;
        }
        if (remainder > 0) {
            result += ' '
            result += `${remainder}`;
        }

        result += '원'

        return result.trim();
    };

    const getRegion = (location) => {
        const words = location.split(' ');
        if (words.length < 2) {
            return location;
        }
        return words.slice(0, 2).join(' ');
    }

    return (
        <div className="table-wrapper">
            <Table responsive className='item-table'>
                <thead>
                    <tr>
                        {fields.map((field, index) => (
                            <th key={index} style={{ position: 'relative' }}>
                                <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                    {/* 정렬 영역 */}
                                    <div 
                                        onClick={() => ['매매가', '전세가', '갭'].includes(field) && handleSort(field)}
                                        style={{ 
                                            display: 'flex', 
                                            alignItems: 'center',
                                            cursor: ['매매가', '전세가', '갭'].includes(field) ? 'pointer' : 'default'
                                        }}
                                    >
                                        {field} {['매매가', '전세가', '갭'].includes(field) && getSortIcon(field)}
                                    </div>

                                    {/* 필터 영역 */}
                                    {field === '갭' && (
                                        <div style={{ marginLeft: '5px' }}>
                                            <IconButton 
                                                onClick={(e) => handleFilterClick(e, '갭')}
                                                style={{ padding: '0', color: 'white' }}
                                            >
                                                <FilterAltIcon fontSize="small" />
                                            </IconButton>
                                            {showGapSlider && (
                                                <div 
                                                    className="gap-slider-container"
                                                    onMouseDown={(e) => e.stopPropagation()}
                                                    onClick={(e) => e.stopPropagation()}
                                                >
                                                    <div className="gap-slider">
                                                        <Slider
                                                            range
                                                            min={0}
                                                            max={50}
                                                            value={gapRange}
                                                            defaultValue={gapRange}
                                                            onChange={handleGapRangeChange}
                                                            onMouseDown={(e) => e.stopPropagation()}
                                                        />
                                                    </div>
                                                    <div className="gap-range-labels">
                                                        <span>{`${gapRange[0]}억`}</span>
                                                        <span>{`${gapRange[1]}억`}</span>
                                                    </div>
                                                    <div className="gap-filter-buttons">
                                                        <Button 
                                                            variant="contained" 
                                                            onClick={handleGapFilterApply}
                                                            className="gap-filter-button"
                                                        >
                                                            필터 적용
                                                        </Button>
                                                    </div>
                                                </div>
                                            )}
                                        </div>
                                    )}
                                    {field === '평' && (
                                    <>
                                        <IconButton onClick={handleFilterClick} style={{ padding: '0', marginLeft: '5%', color: 'white' }}>
                                            <FilterAltIcon fontSize="small" />
                                        </IconButton>
                                        <Menu
                                            className='filter-meunu'
                                            anchorEl={anchorEl}
                                            open={Boolean(anchorEl)}
                                            onClose={handleFilterClose}
                                            style={{ zIndex: 100, minWidth: 'unset', width: 'auto', marginLeft: '-3%', marginRight: '-3%' }}
                                            MenuListProps={{
                                                style: { padding: '0.1%' } // 메뉴 내 패딩 조정
                                            }}
                                        >
                                            <MenuItem value="1">
                                                <Checkbox
                                                    className='filterCheckbox'
                                                    checked={filterValues.indexOf('1') > -1}
                                                    onChange={handleFilterChange('1')}
                                                    size='small'/>                                            
                                                <span className='filterItemText'>10평대</span>
                                            </MenuItem>
                                            <MenuItem value="2">
                                                <Checkbox
                                                    className='filterCheckbox'
                                                    checked={filterValues.indexOf('2') > -1}
                                                    onChange={handleFilterChange('2')}
                                                    size='small'/>
                                                <span className='filterItemText'>20평대</span>
                                            </MenuItem>
                                            <MenuItem value="3">
                                                <Checkbox
                                                    className='filterCheckbox'
                                                    checked={filterValues.indexOf('3') > -1}
                                                    onChange={handleFilterChange('3')}
                                                    size='small'/>
                                                <span className='filterItemText'>30평대</span>
                                            </MenuItem>                                           
                                            <div style={{ padding: '8px', textAlign: 'center' }}>
                                                <Button variant="contained" onClick={handleFilterApply}
                                                    style={{ fontSize: '0.75rem', padding: '5% 10%', backgroundColor: '#509B73' }}>
                                                    필터 적용
                                                </Button>
                                            </div>
                                        </Menu>
                                    </>
                                )}
                                </div>
                            </th>
                        ))}
                    </tr>
                </thead>
            <tbody>
                {datas
                    .filter(row => row[fieldMappings['갭']] >= 0) 
                    .map((row, rowIndex) => (
                        <tr key={rowIndex}>
                            {fields.map((field, colIndex) => (
                                <td key={colIndex}>
                                    {field === '지역' ? (
                                        getRegion(row[fieldMappings['상세 위치']])
                                    ) : ['갭', '매매가', '전세가'].includes(field) ? (
                                        formatCurrency(row[fieldMappings[field]])
                                    ) : field === '아파트명' ? (
                                        <span className="clickable" onClick={() => handleNameClick(row.itemNumber)}>
                                            {row[fieldMappings[field]]}
                                            <OpenInNewIcon fontSize="small" style={{ marginLeft: '5px', fontSize: '0.75rem' }} />
                                        </span>
                                    ) : (
                                        row[fieldMappings[field]]
                                    )}
                                </td>
                            ))}
                        </tr>
                    ))}
            </tbody>
        </Table>
    </div>
);}

export default ItemTable;